import { Component, OnInit } from '@angular/core';
import {Project} from "../../models/project.model";
import {RestDatasourceService} from "../../services/rest-datasource.servics";

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit {
  projects: Project[] = [];

  constructor(public datasource: RestDatasourceService) {
    this.datasource.get('project').subscribe(data => {
      this.projects = data;
      console.log(this.projects);
    });
  }

  ngOnInit() {
  }

  getProject(name: string) {
    console.log(this.datasource.get('project',[{param: 'name',value: name}]));
  }
}



